<template>
  <div :class="['main-navbar', 'bg-white', stickyTop ? 'sticky-top' : '']">
    <d-navbar type="light" class="align-items-stretch flex-md-nowrap p-0">
      <NavbarNav v-if="isAuth" />
      <NavbarToggle v-if="mobile" />
    </d-navbar>
  </div>
</template>

<script>
import { mapState } from "vuex";

import NavbarNav from "./NavbarNav.vue";
import NavbarToggle from "./NavbarToggle.vue";

export default {
  components: {
    NavbarNav,
    NavbarToggle
  },
  props: {
    /**
     * Whether the main navbar should be sticky, or not.
     */
    stickyTop: {
      type: Boolean
    }
  },
  data() {
    return {
      mobile: false
    };
  },
  computed: {
    ...mapState(["isAuth"])
  },
  created() {
    if (window.matchMedia("(max-width: 576px)").matches) {
      this.mobile = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.main-navbar {
  .dropdown-menu {
    display: block;
  }
}
</style>
