<template>
  <d-container fluid class="main-content-container px-4 pb-4">
    <!-- Page Header -->
    <d-row no-gutters class="page-header py-4">
      <!-- Page Header - Page Title -->
      <d-col col sm="4" class="text-center text-sm-left mb-4 mb-sm-0">
        <h3 class="page-title">
          Статистика
        </h3>
      </d-col>

      <!-- Page Header - Datepicker -->
      <d-col lg="4" sm="4" class="offset-sm-4 d-flex align-items-center">
        <d-input-group
          size="sm"
          class="date-range d-flex justify-content-end my-auto"
        >
          <d-datepicker
            v-model="dateRange.from"
            :highlighted="{
              from: dateRange.from,
              to: dateRange.to || new Date()
            }"
            :language="ru"
            placeholder="Выбрать дату"
            typeable
            small
            minimum-view="month"
            @selected="setDateStart"
          />
          <d-input-group-text slot="append">
            <i class="material-icons">&#xE916;</i>
          </d-input-group-text>
        </d-input-group>
      </d-col>
    </d-row>

    <!-- List File Manager -->
    <v-client-table
      class="dataTables_wrapper"
      :data="tableData"
      :columns="columns"
      :options="clientTableOptions"
    >
      <span slot="date" slot-scope="props">
        {{ props.row.date | dateFormat }}
      </span>
    </v-client-table>
  </d-container>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import { ru } from "vuejs-datepicker/dist/locale";
import moment from "moment";
import transactionHistoryData from "@/data/transaction-history-data.json";
import "@/assets/scss/vue-tables.scss";

Vue.use(ClientTable);

export default {
  components: {},
  filters: {
    dateFormat: function(value) {
      return moment(value).format("DD MM YYYY");
    }
  },
  data() {
    return {
      ru: ru,
      dateRange: {
        from: null,
        to: null
      },

      columns: ["total", "price", "date"],

      /**
       * Table Data
       */
      tableData: transactionHistoryData,

      /**
       * Vue Tables Configuration Options
       * @see https://github.com/matfish2/vue-tables-2
       */
      clientTableOptions: {
        perPage: 10,
        recordsPerPage: [10, 25, 50, 100],
        skin: "transaction-history table dataTable",
        sortIcon: {
          base: "fas float-right mt-1 text-muted",
          up: "fa-chevron-down",
          down: "fa-chevron-up"
        },
        headings: {
          total: "Всего запросов",
          price: "Стоимость",
          date: "Дата"
        },
        texts: {
          count:
            "Показывать {from} от {to} из {count} записей|{count} Записей|Одна запись",
          filterPlaceholder: "",
          limit: "Показать"
        },
        pagination: {
          edge: true,
          nav: "scroll"
        },
        dateColumns: ["date"],
        dateFormat: "DD MM YYYY",
        customFilters: [
          {
            name: "dateRange",
            callback: function(row, query) {
              const selectedDate = moment(query);
              const selectedMonth = selectedDate.month();
              const selectedYear = selectedDate.year();
              const currentDate = moment(row.date);
              const currentMonth = currentDate.month();
              const currentYear = currentDate.year();
              return (
                selectedYear === currentYear && selectedMonth === currentMonth
              );
            }
          }
        ]
      }
    };
  },
  methods: {
    setDateStart(val) {
      Event.$emit("vue-tables.filter::dateRange", val);
    }
  }
};
</script>
