<template>
  <div class="main-content-container container-fluid h-100 px-4">
    <d-row no-gutters class="h-100">
      <d-col lg="3" md="5" class="auth-form mx-auto my-auto">
        <d-card>
          <d-card-body>
            <!-- Title -->
            <h5 class="auth-form__title text-center mb-4">
              Войдите или зарегистрируйтесь
            </h5>

            <!-- Form Fields -->
            <d-form>
              <div class="form-group">
                <label for="exampleInputEmail1">Почта</label>
                <d-input
                  id="exampleInputEmail1"
                  type="email"
                  placeholder="Введите email"
                />
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Пароль</label>
                <d-input
                  id="exampleInputPassword1"
                  type="password"
                  placeholder="Введите пароль"
                />
              </div>
              <d-button
                pill
                type="submit"
                class="btn-accent d-table mx-auto"
                @click.prevent="login"
              >
                Войти в систему
              </d-button>
            </d-form>
          </d-card-body>

          <!-- Social Icons -->
          <d-card-footer class="border-top d-flex justify-content-between">
            <d-link tag="a" to="forgot-password">
              Забыли пароль?
            </d-link>
            <d-link tag="a" to="register" class="ml-auto">
              Регистрация
            </d-link>
          </d-card-footer>
        </d-card>

        <!-- Meta Details -->
      </d-col>
    </d-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  methods: {
    ...mapMutations(["IS_AUTH"]),
    login() {
      this.$router.push("/user-profile");
      this["IS_AUTH"](true);
    }
  }
};
</script>
