import Vue from "vue";
import Vuex from "vuex";
import { IS_AUTH } from "./mutation-types";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isAuth: false
  },
  mutations: {
    [IS_AUTH](state, payload) {
      state.isAuth = payload;
    }
  },
  actions: {},
  modules: {}
});
